@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../resources/fonts/Roboto.woff") format("woff"), url("../resources/fonts/Roboto.woff2") format("woff2"), url("../resources/fonts/Roboto.ttf") format("ttf"), url("../resources/fonts/Roboto.svg") format("svg");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto Condensed", sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

main {
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translate(-50%, 0);
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

:root {
  --font-family: "Roboto", sans-serif;
  --content-width: 1140px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-primary: #e3e3e3;
  --grid-gap: 30px;
}

.select {
  position: relative;
  width: 320px;
  height: 48px;
}
.select-header {
  position: relative;
  padding: 16px 0 16px 40px;
  background: rgba(2, 22, 45, 0.0509803922);
  border: 1px solid rgba(7, 0, 15, 0.1019607843);
  border-radius: 12px;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.5 13C10.5376 13 13 10.5376 13 7.5C13 4.46243 10.5376 2 7.5 2C4.46243 2 2 4.46243 2 7.5C2 10.5376 4.46243 13 7.5 13Z' stroke='%23080908' stroke-width='2' stroke-linecap='square'/%3E%3Cpath d='M12 12L14 14' stroke='%23080908' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
  background-position: 16px center;
  background-repeat: no-repeat;
  transition: border 0.3s ease-in-out;
  cursor: pointer;
}
.select-header:hover {
  border: 1px solid rgba(7, 0, 15, 0.4);
}
.select-header.is-active {
  border: 1px solid rgba(7, 0, 15, 0.4);
}
.select-header__location {
  margin: 0;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  color: #07000F;
}
.select-header__choice-count {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 32px;
  height: 32px;
  display: none;
  align-items: center;
  justify-content: center;
  background: #0067FE;
  border-radius: 100%;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: -0.02em;
  color: #fff;
}
.select-header__choice-count.is-choiced {
  display: flex;
}
.select-body {
  position: absolute;
  top: 56px;
  width: 100%;
  display: none;
  background: #fff;
  border: 1px solid rgba(7, 0, 15, 0.1019607843);
  border-radius: 12px;
  box-shadow: 0px 4px 32px 0px rgba(0, 0, 0, 0.0196078431);
}
.select-body.is-open {
  display: block;
}
.select-body__wrapper {
  display: flex;
  align-items: center;
  padding: 14px 0 0 16px;
  border-bottom: 1px solid rgba(7, 0, 15, 0.1019607843);
}
.select-body__wrapper_type_choiced {
  display: none;
  padding: 12px 0 12px 16px;
}
.select-body__wrapper_type_choiced.is-active {
  display: flex;
}
.select-radio {
  padding-bottom: 14px;
  color: rgba(7, 0, 15, 0.4);
}
.select-radio_type_checked {
  color: #07000F;
  border-bottom: 1px solid #07000F;
}
.select-radio:not(:last-child) {
  margin-right: 16px;
}
.select-radio__label {
  display: block;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: -0.02em;
  color: inherit;
  text-transform: uppercase;
  cursor: pointer;
}
.select-choiced {
  display: flex;
  align-items: center;
  overflow-x: scroll;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
}
.select-choiced::-webkit-scrollbar {
  display: none;
}
.select-choiced__item {
  position: relative;
  display: none;
  padding: 11px 32px 11px 12px;
  background: rgba(2, 22, 45, 0.0509803922);
  border: 1px solid rgba(7, 0, 15, 0.1019607843);
  border-radius: 8px;
}
.select-choiced__item.is-active {
  display: block;
}
.select-choiced__item:not(:last-child) {
  margin-right: 4px;
}
.select-choiced__name {
  margin: 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  letter-spacing: -0.02em;
  color: #07000F;
  text-transform: uppercase;
}
.select-choiced__close {
  position: absolute;
  top: 10px;
  right: 12px;
  display: block;
  width: 16px;
  height: 16px;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.91235 4L11.9128 12.0004M12.0843 4L4.08392 12.0004' stroke='%2307000F' stroke-opacity='0.4' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
  cursor: pointer;
}
.select-list {
  display: none;
  height: 250px;
  padding: 8px 0 8px 16px;
}
.select-list_type_active {
  display: block;
}
.select-item {
  position: relative;
  padding: 12px 0;
  cursor: pointer;
}
.select-item__name {
  margin: 0;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #07000F;
}
.select-item__choice {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 32px;
  height: 32px;
  background: #fff;
  border: 1px solid rgba(7, 0, 15, 0.1019607843);
  border-radius: 100%;
}
.select-item__choice_type_choiced {
  background: #0067FE;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 5.72727L4.82353 9L10 3' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
  background-position: center;
  background-repeat: no-repeat;
}

.input__radio {
  display: none;
}